import { View, Text, TouchableWithoutFeedback } from "react-native";
import React from "react";
import {
    ButtonOuter,
    ButtonInner,
    ButtonFaceGradient,
} from "./styles";
import { useCallback, useState, useMemo } from "react";

type ButtonProps = {
    onPress: () => void;
    size: number;
    distance: number;
    blur: number;
    borderRadius: number;
}

function NeumorphismSimpleButton(props: ButtonProps) {
    const { onPress, size = 150, distance = 50, blur = 30, borderRadius = 30 } = props;
    const [isDown, setDown] = useState(false);

    const handlePressIn = useCallback(() => {
        setDown(true);
    }, [setDown]);

    const handlePressOut = useCallback(() => {
        setDown(false);
    }, [setDown]);
    const gradientColors = useMemo(() => isDown ? ['#cacaca', '#ffffff'] : ['#ffffff', '#e0e0e0'], [isDown]);
    const gradientStart = useMemo(() => isDown ? ({ x: 145, y: 125 }) : ({ x: 145, y: 125 }), [isDown]);
    const gradientEnd = useMemo(() => isDown ? ({ x: 145, y: 145 }) : ({ x: 1, y: 145 }), [isDown]);


    return (
        <TouchableWithoutFeedback
            onPress={onPress}
            onPressIn={handlePressIn}
            onPressOut={handlePressOut}>
            {/* <View style={{ width: size + distance, height: size + distance, borderWidth: 1, justifyContent: 'center', alignItems: 'center' }}> */}

            <ButtonOuter borderRadius={borderRadius} blur={blur} distance={distance} size={size}>
                <ButtonInner borderRadius={borderRadius} blur={blur} distance={distance} size={size}>
                    <ButtonFaceGradient borderRadius={borderRadius} start={gradientStart} end={gradientEnd} distance={distance} size={size} colors={gradientColors}  >
                    </ButtonFaceGradient>
                </ButtonInner>
            </ButtonOuter>
            {/* </View> */}

        </TouchableWithoutFeedback>
    );
}

NeumorphismSimpleButton.defaultProps = {

}

export default NeumorphismSimpleButton;
